.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.lightmode{
    background: rgb(246, 246, 246);
    /* background-color: #fff; */
}

.darkmode{
    background: rgb(36, 36, 36)
}
.lightmode, .darkmode{
    overflow: hidden;
    height: 90vh;
}
body{
    padding:0;
    margin: 0 auto;
}
.night_btn{
    margin-right:15px;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
}
.navbar {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    width: 100%;
    display: grid;
    grid-gap: 10px;
    background: rgb(42, 44, 39);
    grid-template-columns: 0fr 0fr 1fr;
}
.light{
    background: #f6f6f6;
}
.light h3{
    background: linear-gradient(45deg, #ff0000 33%, #0048a1 66%, #1bf700);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.light .buttons a{
    color: black !important;
    font-weight:500;
    border-radius: 4px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}
.light .websocketStatus{
    color: green !important;
}
.navbar a{
    text-decoration: none;
}
.navbar .buttons a {
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
}
.navbar .buttons a:hover, .red{
    background: red;
}
.navbar h3{
    margin-left: 15px;
    white-space: nowrap;
    color: rgb(200, 238, 127);
}
.navbar .buttons{
    display: inline-flex;
}
.homeMenu{
    margin-top:20px;
    /* min-height: 100vw; */
    display: grid;
    grid-gap: 70px;
    grid-template-columns: 1fr 0fr;
}
::-webkit-scrollbar{
    display: none;
}
::-webkit-scrollbar {
    width: 6px;
    display: unset;
}

.darkmode::-webkit-scrollbar-track{
    background: #222;
}
::-webkit-scrollbar-track {
    background: rgb(139, 139, 139);
    border-radius: 12px;
}
.darkmode ::-webkit-scrollbar-thumb{
    background-color: #555;
    border: 3px solid #666;
}
::-webkit-scrollbar-thumb {
    background-color: rgb(71, 71, 71);
    border: 3px solid rgb(71, 71, 71);
    border-radius: 12px;
}

::-webkit-scrollbar-corner { background: #222}
.playerDropdown{
    display: contents;
}
.show{
    display: grid !important;
    grid-template-columns: 0fr 0fr;
    grid-gap: 10px;
    position: absolute;
}

.ban, .kick {
    width: 65px;
    /* background-color: red; */
    color: white;
    border: none;
    border-radius: 5px;
    height: 25px;
    font-size: 14px;
    border: 1px solid red;
    background: transparent;
    transition: all 0.35s ease-in 0s;
}

.playerDropdown button:hover {
    background-color: red
}
.lightmode button:hover{
    background-color: rgb(2, 0, 107);
}
.lightmode .ban, .lightmode .kick{
    border:0px;
}
.lightmode .playerMenu{
    background-color: rgb(4, 0, 255);
}
.playerMenu {
    height: 25px;
    padding-top: 10px;
    margin-top:10px;
    padding-bottom: 8px;
    display: none;
    background-color: #3e3e3e;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding-left: 10px;
    padding-right: 14px;
    border-radius: 8px;
}
.darkmode .playersOnline {
    background: rgb(46, 46, 46);
}

.darkmode .playersOnline h3 {
    color: rgb(157, 192, 75);
}

.darkmode .playersOnline ul {
    color: rgb(194, 194, 194);
}
.lightmode .playersOnline{
    border: 1px solid rgb(230, 230, 230);
    background: rgb(221, 221, 221);
}

.playersOnline {
    display: grid;
    justify-self: end;
    margin-right: 30px;
    grid-template-rows: 0fr 1fr;
    width: 300px;
    border: 1px solid rgb(56, 56, 56);
    border-radius: 12px;
    padding: 10px;
    height: 320px;
}

.playersOnline h3 {
    color: black;
    margin: 0 auto;
    text-align: center;
}

.playersOnline ul {
    overflow-y: auto;
    list-style: none;
    padding: 0;
    padding-left: 15px;
}

.playersOnline ul div li{
    margin-bottom: 8px;
}
.lightmode ul li::before{
    color: green !important;
}
.playersOnline ul li::before {
    content: "\2022";
    /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: greenyellow;
    /* Change the color */
    font-weight: bold;
    /* If you want it to be bold */
    display: inline-block;
    /* Needed to add space between the bullet and the text */
    width: 1em;
    /* Also needed for space (tweak if needed) */
    margin-left: -1em;
    /* Also needed for space (tweak if needed) */
}

.playersOnline hr {
    border: none;
    width: 100%;
    height: 1px;
    background: #838383;
}
.worlds{
    border: 1px solid rgb(56, 56, 56);
    margin-left: 20px;
    border-radius: 12px;
    height: 650px;
    max-height: 330px;
    max-width: 865px;
    min-width: 75%;
    justify-self: center;
    background: rgb(46, 46, 46);
    overflow-y: hidden;
}
.lightmode .worlds{
    border: 1px solid rgb(230, 230, 230);
    background: rgb(228, 228, 228);
}
.lightmode .worlds h3{
    color: black;
}
.worlds_list::-webkit-scrollbar {
    height: 10px;
    display: unset;
}
.worlds_list::-webkit-scrollbar-thumb {
    background-color: rgb(71, 71, 71);
    border: 0px solid rgb(71, 71, 71);
    /* border-radius: 12px; */
}
.worlds_list {
    grid-auto-flow: column;
    overflow: auto;
    display: grid;
    grid-gap: 33px;
    padding: 25px;
    max-width: 100%;
}
.lightmode #wName{
    background: black;
    color: white;
}
#wName{
    margin: 0 auto;
    width: 250px;
    border-radius: 35px;
    background: #00fff3;
    color: black;
    word-wrap: break-word;
    margin-bottom: 8px;
}
.worldCart {
    letter-spacing: 0.635px;
    margin-top: 30px;
}
.worldCart table {
    margin: 0px 31%;
    border-spacing: 4px;
    border-collapse: separate;
}
.lightmode .worldCart table th{
    color: black !important;
}
.worldCart table th{
    color: rgb(179, 179, 179);
}
.extend{
    max-height: 560px;
    transition: max-height 1s ease-out;
}
.close{
    transition: max-height 1s ease-in;
    max-height: 330px;
}
.world h2 {
    margin: 0;
    margin-top: -83%;
    z-index: 2;
    position: relative;
    width: 100%;
    text-align: center;
    overflow-wrap: break-word;
}
.worldBG{
    position: relative;
    background-image: url("/world_template.png");
    /* position: fixed; */
    background-size: cover;
    -webkit-filter: blur(4px);
    -moz-filter: blur(4px);
    -o-filter: blur(4px);
    -ms-filter: blur(4px);
    filter: blur(4px);
    height: 100%;
    left: 0;
    right: 0;
    z-index: 1;
    display: block;
}
.world:hover{
    color:white;
    transition: 0.5s;
    cursor: pointer;
    box-shadow: 0 1px 12px rgba(0,0,0, 1);
}
.worldBG:hover{
    transition: 0.5s;
    -webkit-filter: blur(6px);
    -moz-filter: blur(6px);
    -o-filter: blur(6px);
    -ms-filter: blur(6px);
    filter: blur(6px);
}
.world{
    background-color: transparent;
    box-shadow: 0 1px 12px rgba(0,0,0, 0.5);
    border-radius: 16px;
    width: 150px;
    height: 200px;
    margin: 0 auto;
}
.worlds h3{
    text-align: center;
    color: white;
    font-size:22px;
    margin:6px;
    font-weight:600;
}
.worlds hr{
    color: black;
    border: none;
    width: 95%;
    height: 1px;
    background: #838383;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

